export default {
  name: 'activityItem',
  props: {
    src: {
      type: [String],
      default: require('@/static/images/wallet-recharge-card.png'),
    }, // 海报
    title: {
      type: String,
      default: '',
    }, // 名称
    guest: {
      type: String,
      default: '',
    }, // 嘉宾
    time: {
      type: String,
      default: '',
    }, // 日期
    address: {
      type: String,
      default: '',
    }, // 地址
    price: {
      type: [String, Number],
      default: '',
    }, // 价格
    status: {
      type: [Number, String],
      default: 1,
    }, // 状态 1:可报名 2:已报名 3：已使用 4：已过期
  },
  computed: {
    buttonText() {
      let text = ''
      switch (parseInt(this.status)) {
        case 1:
          text = '去报名'
          break;
        case 2:
          text = '票券'
          break;
        case 3:
          text = '查看'
          break;
        case 4:
          text = '查看'
          break;
      }
      return text
    },
  },
  methods: {
    handleClick(value) {
      switch (value) {
        case 'item':
          this.$emit('click')
          break;
        case 'button':
          this.$emit('buttonClick')
          break;
      }
    },
  },
}
